body {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}
.gugu_widget {
  max-width: 360px;
  position: fixed;
  right: 30px;
  bottom: 80px;
}
.gugu_widget__app {
  font-size: 16px;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  color: #222;
  box-shadow: 2px 2px 12px 0 rgba(0, 0, 0, 0.07);
  border-radius: 12px;
  overflow: hidden;
}

.gugu_widget__app a {
  color: #5271FF;
  transition: 0.2s ease-in-out all;
}

.gugu_widget__header {
  margin: 0;
  display: block;
  font-size: 19px;
  padding: 12px 6px;
  background: #5271FF;
  color: white;
  text-align: center;
}

.gugu_widget__header a {
  color: #fff;
}

.gugu_widget__inner {
  padding: 12px;
}

.gugu_widget__inner a {
  color: #222;
  text-decoration: none;
}

.gugu_widget__post {
  display: flex;
  flex-direction: column;
  padding: 6px 0;
}


.gugu_widget__posted_by {
  font-size: 14px;
  color: rgb(120, 124, 126);
  padding: 0 0 6px 0;
}

.gugu_widget__posted_by a {
  color: rgb(120, 124, 126);
}

a.gugu_widget__posted_by:hover {
  text-decoration: underline;
}

a.gugu_widget__title {
  font-weight: 500;
}

a.gugu_widget__title:hover {
  color: #000;
}

.gugu_widget__powered_by {
  text-align: center;
  font-size: 14px;
}